@import "src/styles/vars";

.post {
  padding: 24px;
  border-radius: 4px;
  gap: 16px;
  background-color: $LightWhite;
}

.informationPostBlock {
  display: flex;
  align-items: center;
  gap: 16px;
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid $Gray200;
  cursor: pointer;
}

.avatarPostCreator {
  img {
    border-radius: 1000px;
    width: 56px;
    height: 56px;
  }
}

.nameBlock {
  width: 82%;
  display: flex;
  flex-direction: column;
}

.deleteBasketBlock {
  img {
    opacity: .5;
    width: 18px;
  }
}

.name {
  line-height: 28px;
  cursor: pointer;
}

.timePosted {
  font-size: 12px;
  line-height: 15px;
  color: $HelperGray;
}

.textPostBlock {
  p {
    white-space: pre-line;
    line-height: 17px;
  }
}

.iconsPostBlock {
  margin-top: 16px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  gap: 36px;
}

.repostInformation {
  display: flex;
  gap: 6px;
  align-items: center;
  width: 100%;
  span {
    font-size: 12px;
    color: $HelperGray;
  }
  img {
    width: 18px;
    opacity: .4;
  }
}
